// BackButton.tsx
import React, { useEffect } from "react";
import { Box, Button } from "@mui/material";
import { createRoot } from "react-dom/client";

interface BackButtonProps {
  onClick: () => void;
  children?: React.ReactNode;
  homeButton?: boolean;
}

const BackButton: React.FC<BackButtonProps> = ({ onClick, children, homeButton }) => {
  useEffect(() => {
    // Find the UI overlay container (this exists already in App.tsx)
    const uiOverlay = document.getElementById("ui-overlay");
    if (!uiOverlay) {
      console.error("UI overlay not found!");
      return;
    }

    // Find or create the dedicated backButton container
    let backButtonContainer = document.getElementById("backButton-container");
    if (!backButtonContainer) {
      backButtonContainer = document.createElement("div");
      backButtonContainer.id = "backButton-container";
      // Optionally add some styling to the container if needed:
      backButtonContainer.style.position = "absolute";
      backButtonContainer.style.top = "1rem";
      backButtonContainer.style.left = "1rem";
      backButtonContainer.style.display = "flex";
      backButtonContainer.style.flexDirection = "rows";
      backButtonContainer.style.pointerEvents = "none";
      backButtonContainer.style.gap = "1rem";
      uiOverlay.appendChild(backButtonContainer);
    }

    // Create a new container for this BackButton instance
    const buttonDiv = document.createElement("div");
    // (No special styling here—the Box inside will position the button.)

    // Create a React root for this button and render it
    const root = createRoot(buttonDiv);
    root.render(
      <Box
        sx={{
          position: "relative",
          pointerEvents: "auto", // Ensure the button receives clicks
          zIndex: 900,
        }}
      >
        <Button
          onClick={onClick}
          color="primary"
          variant="text"
          id={homeButton ? "home-button" : "back-button"}
          style={{
            color: "white",
            backgroundColor: "rgba(0, 0, 0, 0.8)",
            pointerEvents: "auto",
          }}
        >
          {children}
        </Button>
      </Box>
    );

    // Append the new button container to the backButton container
    backButtonContainer.appendChild(buttonDiv);

    // Cleanup: unmount and remove the button container when the component unmounts
    return () => {
      root.unmount();
      if (backButtonContainer.contains(buttonDiv)) {
        backButtonContainer.removeChild(buttonDiv);
      }
    };
  }, [onClick, children, homeButton]);

  return null; // Nothing is rendered in the normal React tree.
};

export default BackButton;
